@import "stylesheets/sdx/utilities/screenreaders";
@import "../../globals/scss/label";

.component {
  @include bp-sm {
    max-width: 320px;
  }

  .wrapper {
    display: flex;

    .input {
      &:nth-of-type(1),
      &:nth-of-type(2) {
        flex: 2;
      }

      &:nth-of-type(3) {
        flex: 3;
      }
    }

    .separator {
      align-self: end;
      text-align: center;
      width: $baseline-2;
      margin-bottom: $baseline + 4px; // fine adjustment
    }
  }
}
