import {
  Component,
  h,
  Element,
  Prop,
  Watch,
  Method,
  Fragment,
} from "@stencil/core"
import parse from "date-fns/parse"
import isValid from "date-fns/isValid"
import {
  translate,
  getAppearance,
} from "../../core/helpers/webcomponent-helpers"

// Could be made public in the future
type Part = {
  label: string
  placeholder: string
  maxLength: number
  validationMessage: string
  validate(value: string): boolean
}

interface PartInternal extends Part {
  valid?: boolean
  el?: HTMLSdxInputElement
}

@Component({
  tag: "sdx-input-mask",
  styleUrl: "input-mask.scss",
  shadow: true,
})
export class Input {
  private parts: PartInternal[] = [
    {
      label: "Day",
      placeholder: "DD",
      maxLength: 2,
      validationMessage: "Please enter a valid day",
      validate: (value) => {
        const valueAsNumber = parseFloat(value)
        return valueAsNumber >= 1 && valueAsNumber <= 31
      },
    },
    {
      label: "Month",
      placeholder: "MM",
      maxLength: 2,
      validationMessage: "Please enter a valid month",
      validate: (value) => {
        const valueAsNumber = parseFloat(value)
        return valueAsNumber >= 1 && valueAsNumber <= 12
      },
    },
    {
      label: "Year",
      placeholder: "YYYY",
      maxLength: 4,
      validationMessage: "Please enter a valid year",
      validate: (value) => {
        return value.length === 4
      },
    },
  ]

  private internalValidationChangeInProgress = false
  private isValidationSetInternally = false

  // A "native" hidden DOM element that is submitted when the item is used in a
  // traditional form.
  private lightDOMHiddenFormInputEl?: HTMLInputElement

  /**
   * Emitted whenever user is typing or clearing the input.
   * @event
   */
  // @ts-ignore just for documentation purposes
  private static eventInput = "input"

  @Element() public el!: HTMLSdxInputElement

  /**
   * Description text read by the screen reader.
   */
  @Prop() public srHint: string = ""

  @Prop() public type?: "ddmmyyyy"

  /**
   * Can be used for both reading and writing the value.
   */
  @Prop({ mutable: true }) public value: string = ""

  /**
   * Text that will be written on the top.
   */
  @Prop({ reflect: true }) public label?: string

  /**
   * Name parameter (useful when the component is used in a traditional HTML
   * form submit).
   */
  @Prop() public name?: string

  /**
   * Set this to false to declare the component as invalid and use the
   * `validation-message` attribute to explain why.
   */
  @Prop({ mutable: true }) public valid?: boolean

  /**
   * Text that explains the validation status to the user.
   */
  @Prop({ mutable: true }) public validationMessage?: string

  /**
   * Marks the component as required (please note that this itself does not
   * handle validation &mdash; use the "valid" and "validation-message" for
   * that).
   */
  @Prop() public required: boolean = false

  /**
   * Focuses the input field as soon as it's rendered.
   */
  @Prop() public autofocus: boolean = false

  @Watch("value")
  public valueChanged() {
    this.updateHiddenFormInputEl()
  }

  @Watch("name")
  public nameChanged() {
    this.updateHiddenFormInputEl()
  }

  @Watch("valid")
  @Watch("validationMessage")
  public validationChanged() {
    // Know whether the consumer or the component changed the prop
    if (this.internalValidationChangeInProgress) {
      this.isValidationSetInternally = true
    } else {
      this.isValidationSetInternally = false
    }
  }

  @Method()
  public async doFocus() {
    this.parts[0].el?.doFocus()
  }

  constructor() {
    this.updateHiddenFormInputEl()
  }

  public componentDidLoad() {
    if (this.autofocus) {
      // timeout because of https://github.com/ionic-team/stencil/issues/3246
      setTimeout(() => {
        this.doFocus()
      })
    }
  }

  private updateHiddenFormInputEl(): void {
    if (!this.lightDOMHiddenFormInputEl) {
      this.lightDOMHiddenFormInputEl = document.createElement("input")
      this.lightDOMHiddenFormInputEl.type = "hidden"
      this.el.append(this.lightDOMHiddenFormInputEl)
    }

    // Clean up properties added previously in case they've changed
    this.lightDOMHiddenFormInputEl.removeAttribute("name")
    this.lightDOMHiddenFormInputEl.removeAttribute("value")

    if (this.name) {
      this.lightDOMHiddenFormInputEl.name = this.name
      this.lightDOMHiddenFormInputEl.value = this.value
    }
  }

  private validate(focussed: boolean) {
    const hasValidation =
      this.valid !== undefined || this.validationMessage !== undefined

    if (hasValidation && !this.isValidationSetInternally) {
      // The properties provided by the consumer are considered
      // stronger and should therefore not be overwritten
      return
    }

    if (focussed) {
      // Do nothing while the user is still busy filling in all fields
      return
    }

    const parts = this.parts
    const someEmpty = parts.some((part) => !part.el?.value)
    const allEmpty = parts.every((part) => !part.el?.value)

    // Make sure validation starts from scratch every single time
    parts.forEach((part) => (part.valid = undefined))
    this.setValidation(undefined, undefined)

    if (someEmpty) {
      if (allEmpty) {
        // It's okay to have all empty
        return
      }

      parts.forEach((part) => (part.valid = false))

      this.setValidation(
        false,
        translate("Please enter a valid date", this.el.lang)
      )

      return
    }

    // Validate all fields individually
    parts.forEach((part) => {
      if (part.el && part.el.value) {
        part.valid = part.validate(part.el.value)
      }
    })

    if (parts.filter((part) => part.valid === false).length > 1) {
      // If more than one field is invalid, the whole date is invalid
      parts.forEach((part) => (part.valid = false))

      this.setValidation(
        false,
        translate("Please enter a valid date", this.el.lang)
      )

      return
    }

    // Show validation message if needed
    for (const part of parts) {
      if (part.valid === false) {
        this.setValidation(
          false,
          translate(part.validationMessage, this.el.lang)
        )

        return
      }
    }

    // Validate all fields fields holistically
    if (!isValid(parse(this.value, "dd.MM.yyyy", new Date()))) {
      parts.forEach((part) => (part.valid = false))

      this.setValidation(
        false,
        translate("Please enter a valid date", this.el.lang)
      )

      return
    }
  }

  private save() {
    // Empty value should not be ".."
    if (this.parts.every((part) => part.el?.value === "")) {
      this.value = ""
      return
    }

    this.value = this.parts.map((part) => part.el?.value).join(".")
  }

  private setValidation(
    valid: boolean | undefined,
    validationMessage: string | undefined
  ) {
    this.internalValidationChangeInProgress = true
    this.valid = valid
    this.validationMessage = validationMessage
    this.internalValidationChangeInProgress = false
  }

  private getLabelClassNames() {
    return {
      label: true,
      [getAppearance(this.el)]: true,
    }
  }

  private getComponentClassNames() {
    return {
      component: true,
      [this.type || ""]: true,
    }
  }

  public render() {
    if (!this.type) {
      return
    }

    const parsedValue = this.value.split(".")

    return (
      <div class={this.getComponentClassNames()}>
        {this.label && (
          <label class={this.getLabelClassNames()}>
            <span>
              {this.label}
              {this.required && <span aria-hidden="true">&nbsp;*</span>}
            </span>
          </label>
        )}

        <div class="wrapper">
          {this.parts.map((part, i) => {
            const valid = this.isValidationSetInternally
              ? part.valid
              : this.valid

            return (
              <Fragment>
                <sdx-input
                  class="input"
                  value={parsedValue[i]}
                  label={translate(part.label, this.el.lang)}
                  placeholder={translate(part.placeholder, this.el.lang)}
                  maxlength={part.maxLength}
                  inputmode="decimal"
                  valid={valid === false ? false : undefined}
                  aria-describedby={
                    i === 0 ? "additional-information" : undefined
                  }
                  onKeyDown={(e) => {
                    if (e.key === ".") {
                      if (part.el?.value) {
                        this.parts[i + 1]?.el?.doFocus()
                      }
                    }
                  }}
                  onKeyPress={(e) => {
                    if (!isFinite(+e.key)) {
                      // Allow only numbers
                      e.preventDefault()
                    }
                  }}
                  onInput={() => this.save()}
                  onBlur={(e) => {
                    const els = this.parts.map((part) => part.el)
                    const focussed = els.includes(
                      e.relatedTarget as HTMLSdxInputElement
                    )
                    this.validate(focussed)
                  }}
                  inputStyle={{
                    textAlign: "center",
                  }}
                  ref={(ref) => (this.parts[i].el = ref)}
                />

                {i < this.parts.length - 1 && <span class="separator">.</span>}
              </Fragment>
            )
          })}
        </div>

        <div id="additional-information">
          {this.srHint && <span class="sr-only">{this.srHint}</span>}

          {this.validationMessage && (
            <Fragment>
              {/* extra span needed because validation-message is hidden on focus and therefore not read by screen reader */}
              <span class="sr-only">{this.validationMessage}</span>

              <sdx-validation-message
                validationMessage={this.validationMessage}
                aria-hidden="true"
              />
            </Fragment>
          )}
        </div>
      </div>
    )
  }
}
